import { IViewActionDesignManager, IViewActionDesignProperties } from '@helix/platform/view/api';
import { IViewComponentDesignValidationIssue } from '@helix/platform/view/designer';
import { Observable, of } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class RxUnknownViewActionDesignManager implements IViewActionDesignManager {
  validate(
    actionProperties: IViewActionDesignProperties,
    propertyName: string
  ): Observable<IViewComponentDesignValidationIssue[]> {
    return of([
      {
        type: 'error',
        description: `Unknown action: ${actionProperties.name}`,
        propertyName
      }
    ]);
  }
}
